.ItemShop{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin: 0 auto;
    justify-content: center;
    max-width: 1000px;
    margin-top: 50px;
    padding-bottom: 50px;
}
@media(max-width:1000px){
    .item {
        max-width: 970px;
    }
}
@media(max-width:992px){
    .item{
        max-width: 750px;
    }
}
.item{
    margin-top: 50px;
    margin-right: 50px;
    margin-left: 50px;
}
.itemImg:hover{
    transform:scale(1.3)
}
.itemImg{
    margin-top: 20px;
    transition: 1s;
    display: flex;
    margin: 0 auto;
}
.ItemShop a{
    display: flex;
    justify-content: center;
    margin-top: 20px;
    font-size: 20px;
    margin-top: 20px;
}
.btn{
    display: flex;
    margin: 0 auto;
    margin-top: 20px;
    font-size: 15px;
    background-color: yellow;
    color: black;
}
.btn:hover{
    cursor: pointer;
    transform: scale(1.1);
}
.navItem{
    color: hotpink;
    text-decoration: none;
}